.navbar.fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1030;
}
@media (min-width: 768px) {
  .navbar.fixed {
    position: fixed;
    bottom: 0;
    width: 222px;
    flex-flow: column nowrap;
    align-items: flex-start;
  }
  .navbar.fixed .navbar-collapse {
    flex-grow: 0;
    flex-direction: column;
    width: 100%;
  }
  .navbar.fixed .navbar-collapse .navbar-nav {
    flex-direction: column;
    width: 100%;
  }
  .navbar.fixed .navbar-collapse .navbar-nav .nav-item {
    width: 100%;
  }
  .navbar.fixed .navbar-collapse .navbar-nav .nav-item .dropdown-menu {
    top: 0;
  }
}
@media (min-width: 768px) {
  .navbar.fixed {
    right: auto;
  }
  .navbar.fixed .navbar-nav .nav-item .dropdown-toggle:after {
    border-top: 0.3em solid transparent;
    border-left: 0.3em solid;
    border-bottom: 0.3em solid transparent;
    border-right: none;
    vertical-align: baseline;
  }
  .navbar.fixed .navbar-nav .nav-item .dropdown-menu {
    left: 100%;
  }
}
